@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600&display=swap);
*{--speed: 400ms;font-family:ui-sans-serif,-apple-system,BlinkMacSystemFont,"Segoe UI",Helvetica,"Apple Color Emoji",Arial,sans-serif,"Segoe UI Emoji","Segoe UI Symbol";padding:0;scroll-behavior:smooth;-webkit-tap-highlight-color:transparent;line-height:1.42857;letter-spacing:-0.005em;--drawer-width-open: 240px;--drawer-width-closed: 0px}body{background-color:var(--color-background-2);color:var(--font-color)}a{color:var(--link-color)}ul li{list-style:disc}ul li a{text-decoration:none;color:var(--main-color);font-weight:400}.logo_container{display:flex;align-items:center;grid-gap:10px;gap:10px;text-decoration:none}.logo_container h3{font-size:24px;font-weight:400;color:var(--main-color)}.logo_container p{font-weight:400;color:var(--main-color)}.logo_inner{height:40px;width:40px;background-color:var(--main-color);border-radius:50%;display:grid;place-items:center}.logo_inner_inner{height:30px;width:30px;background-color:var(--main-color);border-radius:50%}h4{font-weight:400;color:rgba(0,0,0,.5)}textarea:focus,input:focus{outline:none}textarea{resize:none}.toggle__drawer__button{width:30px;position:absolute;left:10px;height:30px;border-radius:50%;border:none;display:flex;align-items:center;justify-content:center;font-size:1.8rem;background-color:transparent;cursor:pointer;transition:.2s all;padding:4px;color:var(--font-color)}.toggle__drawer__button:hover{background-color:var(--color-background-1)}.list{display:flex;flex-direction:column;align-items:center;justify-content:center;list-style:none;padding:0px;width:100%}.list-item{height:35px;border-radius:0px;width:100%;margin:auto;display:flex;flex-direction:row;cursor:pointer;align-items:center;justify-content:space-between}.list-item>*{width:100%;margin:12px !important;display:flex;color:var(--font-color)}.list-item:hover{background-color:rgba(128,128,128,.1)}.active{background-color:rgba(128,128,128,.1)}
.accordion__menu__section{display:flex;flex-direction:column;width:auto}.accordion__menu{border:none;outline:none;color:var(--font-color);cursor:pointer;padding:10px;display:flex;align-items:center;transition:background-color var(--speed) ease}.accordion__menu__title{font-family:"Open Sans",sans-serif;font-weight:600;font-size:14px;text-align:left}.accordion__menu__icon{margin-left:auto;transition:transform var(--speed) ease}.active-menu__item{border-bottom:solid 1px var(--main-color);background-color:transparent}.rotate{transform:rotate(90deg)}.accordion__menu__content{overflow:hidden;transition:max-height var(--speed) ease;background-color:var(--color-background-1)}.accordion__menu__text{font-size:14px;background-color:var(--color-background-2)}
.sticky__side__nav{width:auto;background-color:var(--background-color-2);height:100%}
